@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Playfair+Display:ital,wght@0,400..900;1,400..900&family=Unlock&display=swap');
.body{
    background: linear-gradient(135deg, rgba(60, 180, 155, 0.8), rgba(225, 130, 160, 0.8));
    margin: 0;
    padding: 0;
    
}
.bodyhome{
    margin: 0;
    padding: 0;

}
.app{
    display: flex;
    flex-direction: column;
}
.header{
    display:flex;
    flex-direction: row;
    background-color: #ffffff;
    padding: 25px;
    justify-content: flex-end;
    position: fixed;  
    top: 0;  
    width: 100%;  
}
.buttons{
    background-color: #ffffff;
    border:none; 
    transition: transform .1s;
    border-radius:10px; 
    padding:10px;
    min-height:10px; 
    min-width: 80px;
    margin: 0 5px
}
.buttonreg{
    background-color: #303030;
    color: #ffffff;
    border:#ffffff;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    transition: transform .1s;
    border:none; 
    border-radius:10px; 
    padding:10px;
    min-height:10px; 
    min-width: 80px;
    margin: 0 40px 0 5px
}
.buttoncr{
    background-color: #303030;
    color: #ffffff;
    border:#ffffff;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    transition: transform .1s;
    border:none; 
    border-radius:10px; 
    padding:10px;
    min-height:10px; 
    min-width: 80px;
    margin: 0 5px
}
.buttonlog{
    background-color:#d0d0d0;
    color: #303030;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    transition: transform .1s;
    border:none; 
    border-radius:10px; 
    padding:10px;
    min-height:10px; 
    min-width: 80px;
    margin: 0 5px
}
.buttonlog:hover{
    opacity: 0.7;
    cursor: pointer;
    transform: scale(1.1);
}
.buttonreg:hover{
    opacity: 0.7;
    cursor: pointer;
    transform: scale(1.1);
}
.buttons:hover{
    background-color: #f0f0f0;
    cursor: pointer;
    transform: scale(1.1);
}
.buttoncr:hover{
    opacity: 0.7;
    cursor: pointer;
    transform: scale(1.1);
}
.Center{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20%;
}
.centerC{
    display: flex;
    font-size: 80px;
    text-shadow: 0 0 2px #fff, 
             -1px -1px 0 rgba(60, 180, 155, 0.8),
             -2px -2px 1px hsl(80, 10%, 15%);
    margin: -20px 0px
    
}
.centerB{
    display: flex;
    opacity: 0.6;
}
.underapp{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Under{
    display:flex;
    color: #212020;   
    width: 800px;
    align-items: left;
    background-color: #f0f0f0;
    border-radius: 30px 30px 0px 0px;
}
.undet_text{
    font-size: 10px;
    margin: 15px 25px;
    opacity: 0.7;
}
.line{
    border-bottom: 1px solid #303030;
    width: 750px;
}

.str{
    padding: 100px;
}
.form{
    background-color: #d0d0d0;
    width: 100%;
    height: 100%;
    
}
.back{
    font-family: "Libre Baskerville", serif;
    font-weight: 300;
    font-style: normal;
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, rgba(60, 180, 155, 0.8), rgba(225, 130, 160, 0.8));
    
}

.Loginbody{
    border-radius: 20px;
    margin-top: 5%;
    max-height: 100vh;
    
 }
 .Loginbody1{
    width: 420px;
    background: #f0f0f0;
    border-radius: 10px;
    padding: 30px 40px;
 }

.head{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100% ;
    margin-top: -40px;
}

.underline{
    width: 40px;
    height: 4px;
    background: #000000;
    border-radius: 9px;
    margin: -15px 0px 10px 0px;
 }

 .Loginbody .inputbox{
    position: relative;
    width: 100%;
    height: 50px;
    margin: 20px 0px;
 }

 .Loginbody input{
    font-family: "Libre Baskerville", serif;
    font-weight: 300;
    font-style: normal;
    width: 100%;
    height: 100%;
    background: #dcdbdb;
    border: none;
    outline: none;
    border: none;
    border-radius: 10px;
    font-size: 14px;
    color: #000000;
    padding: 0px 0px 0px 15px;
 }
.inputbox input::placeholder{
    color: #000000;
}
.inputbox .icon{
    position: absolute;
    right: 0px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
}
.buttonll{
    background-color:#d0d0d0;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    font-size: 16px;
    color: #303030;
    transition: transform .1s;
    border:none; 
    border-radius:10px; 
    padding:15px;
    min-height:20px; 
    min-width: 150px;
    margin: 0 5px
}
.buttonrr{
    background-color:#303030;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    font-size: 16px;
    color: #f0f0f0;
    transition: transform .1s;
    border:none; 
    border-radius:10px; 
    padding:15px;
    min-height:40px; 
    min-width: 150px;
}
.butlogcent{
    display: flex;
    align-items: center;
    justify-content: center;

}
.buttonll:hover{
    opacity: 0.7;
    cursor: pointer;
    transform: scale(1.1);
}
.buttonrr:hover{
    opacity: 0.7;
    cursor: pointer;
    transform: scale(1.1);
}
.remember{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    padding: 15px 0px 15px 0px;
}
.ifu{
    display: flex;
    justify-content: end;
    align-items: center;
    margin: 0px -60px 0px 0px;
}
.check{
    display: flex;
    flex-direction: row;
}
.out{
    border:none; 
    
}
.buttonout{
    background: #303030;
    font-size: 13px;
    cursor: pointer;
    color: #f0f0f0 ;
    transition: transform .1s;
    border:none; 
    border-radius:10px; 
    min-height:40px; 
    min-width: 150px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.buttonout:hover{
    opacity: 0.9;
    cursor: pointer;
    transform: scale(1.1);
}